import React from 'react'
import { IActivity } from '../../../app/models/activity';
import { Segment, Grid, Icon } from 'semantic-ui-react';
import { observer } from 'mobx-react-lite';
import Moment from 'react-moment';

const ActivityDetailedInfo: React.FC<{ activity: IActivity }> = ({ activity }) => {
    return (
        <Segment.Group>
              <Segment attached='top'>
                <Grid>
                  <Grid.Column width={1}>
                    <Icon size='large' color='teal' name='info' />
                  </Grid.Column>
                  <Grid.Column width={15}>
                    <p>{activity.description}</p>
                  </Grid.Column>
                </Grid>
              </Segment>
              <Segment attached>
                <Grid verticalAlign='middle'>
                  <Grid.Column width={1}>
                    <Icon name='calendar' size='large' color='teal' />
                  </Grid.Column>
                  <Grid.Column width={15}>
                    <span>
                        <Moment format="DD/MM/YYYY">{activity.date}</Moment> at <Moment format="h:mm:ss a">{activity.date}</Moment>
                    </span>
                  </Grid.Column>
                </Grid>
              </Segment>
              <Segment attached>
                <Grid verticalAlign='middle'>
                  <Grid.Column width={1}>
                    <Icon name='marker' size='large' color='teal' />
                  </Grid.Column>
                  <Grid.Column width={11}>
                    <span>
                        {activity.venue}, {activity.city}
                    </span>
                  </Grid.Column>
                </Grid>
              </Segment>
            </Segment.Group>
    )
}

export default observer(ActivityDetailedInfo);